import React from "react"
import { Container, Row, Col, Modal, ModalBody} from 'reactstrap';

import { Link } from "gatsby"

import Image from "../components/image";
import Layout from "../components/layout";
import SEO from "../components/seo";
import 'bootstrap/dist/css/bootstrap.min.css';
import LeadershipContent from "../content/leadership.json";


class Executive_Board extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isHovering: false,
        };
        this.propertyClicked = this.propertyClicked.bind(this);
    }

    propertyClicked() {
        this.setState(this.toggleHoverState);
    }

    toggleHoverState(state) {
        return {
            isHovering: !state.isHovering,
        };
    }

    render(){
        return (
            <Layout>
                <SEO title="Executive Board" />
                <Container>
                    <Row className="text-center">
                    {LeadershipContent.individuals.map((data, index) => {
                        return (
                            <div >
                                <Col onClick={this.propertyClicked}>
                                    <Image photoToUse={data.photo}/>
                                    <h3>
                                        <Link to={data.name.replace(/\s+/g, '-').toLowerCase()}>
                                            {data.name}
                                        </Link>
                                    </h3>
                                    <p>
                                        {data.position}
                                    </p>
                                </Col>
                            </div>
                        )
                    })}
                    </Row>
                </Container>
            </Layout>
        )
    }
}

export default Executive_Board;
